.app{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #FFF;
  
}

.box{
  width: 600px;
  background-color: rgb(33, 40, 50);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 40px;
  border: 1px solid #fff;
  border-radius: 15px;
}

h1{
  font-size: 50px;
}

span{
  margin: 12px 0;
}

.box-input{
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.box-input input{
  width: 100%;
  margin-bottom: 12px;
  height: 46px;
  background-color: rgb(38, 45, 55);
  border: 0;
  border-radius: 6px;
  color: #FFF;
  padding: 0 12px;
  font-size: 18px;
  border: 1px solid #fff;
}

.box-input button{
  width: 100%;
  height: 46px;
  border: 0;
  border-radius: 6px;
  background-color: rgb(252, 118, 19);
  color: #FFF;
  font-size: 18px;
  margin-top: 15px;
}

h2{
  padding-top: 30px;
}